.App {
  text-align: center;
  display: grid;
  grid-template-rows: 55% 45%;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}
.main{
  grid-row: 1/2;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  user-select: none;
  cursor: url('cursor.png') 39 39, auto;
}
.date{
  width: 65%;
  height: 80%;
}
.scrollingcontent{
  width: 100%;
  height: 100%;
  background-color: black;
}
.scrollwrap{
  width: 100%;
  height: 100%;
}
.slider{
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 25% 75%;
}
.sliderimage{
  height: 100%;
  width: 100%;
  background-position: center;
  background-size: cover;
  grid-row: 1/2;
  grid-column: 1/2;
}
.sliderinfo{
  grid-row: 1/2;
  grid-column: 2/3;
  margin: 20px;
}
.footer{
  grid-row: 2/3;
  display: grid;
}
.calendar{
  width: 40%;
  display: grid;
  align-self: center;
  justify-self: center;
}
.calendar-header{
  grid-column: 2/3;
  grid-row: 1/2;
  width: calc(100% + 68px);
  justify-self: center;
  background-color: black;
  color: white;
  height: 27px;
}
.calendar-footer{
  grid-column: 2/3;
  grid-row: 3/4;
  width: calc(100% + 68px);
  justify-self: center;
  background-color: black;
  color: white;
  height: 27px;
}
.nexttoggle{
  grid-column: 3/4;
  grid-row: 1/4;
  background-color: black;
  color: white;
  width: 34px;
  height: calc(100% - 54px);
  justify-self: start;
  align-self: center;
  display: grid;
  align-content: center;
  writing-mode: vertical-lr;
}
.prevtoggle{
  grid-column: 1/2;
  grid-row: 1/4;
  background-color: black;
  color: white;
  width: 34px;
  height: calc(100% - 54px);
  justify-self: end;
  align-self: center;
  display: grid;
  align-content: center;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
}
.calendar-header,
.calendar-footer,
.prevtoggle,
.nexttoggle{
  cursor: pointer;
}