.rowsdiv{
    justify-self: center;
    width: 100%;
}
.row{
    justify-self: end;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: 40px;
    background-color: white;
}
.column{
    flex-grow: 1;
    flex-basis: 0;
    max-width: 100%;
}
.cell{
    color: black;
    border: 1px solid black;
    margin: 1px;
    font-family: 'muli';
}
.disabled,
.dis-selected{
    color: #808e9b;
}
.cell {
    position: relative;
    overflow: hidden;
    cursor: pointer;
    background: white;
    transition: 0.25s ease-out;
    display: grid;
    pointer-events: none;
}
.selected,
.cell:active {
    background-color: black;
    transition: 0.5s ease-out;
    pointer-events: initial;
}
.dis-selected,
.dis-selected:active,
.disabled:active {
    background-color: #808e9b;
    transition: 0.5s ease-out;
}
.disabled{
    pointer-events: none;
}
.current{
    background-color: darkslategray;
    transition: 0.5s ease-out;
}
.available{
    background-color: lightgreen;
    transition: 0.5s ease-out;
    pointer-events: initial;
}
.cell:active .number,
.selected .number,
.dis-selected .number,
.current .number{
    visibility: hidden;
}
.cell .number{
    justify-self: center;
    align-self: center;
    grid-row: 1/2;
    grid-column: 1/2;
    font-weight: bold;
    font-size: 14px;
}
.cell .bg {
    font-weight: 700;
    line-height: 1;
    color: #d2dae2;
    opacity: 0;
    font-size: 14px;
    transition: 0.25s ease-out;
    justify-self: center;
    align-self: center;
    grid-row: 1/2;
    grid-column: 1/2;
}
.cell:active .bg,
.selected .bg,
.current .bg  {
    opacity: 1;
    transition: 0.5s ease-in;
    animation: neon .08s ease-in-out infinite alternate;
}
.disabled:active .bg,
.dis-selected .bg,
.dis-selected:active .bg {
    opacity: 1;
    transition: 0.5s ease-in;
    animation: neon-dis .08s ease-in-out infinite alternate;
}
@keyframes neon {
  from {
    text-shadow:
    0 0 6px rgba(202,228,225,0.92),
    0 0 30px rgba(202,228,225,0.34),
    0 0 12px rgba(239, 87, 119,0.52),
    0 0 21px rgba(239, 87, 119,0.92),
    0 0 34px rgba(239, 87, 119,0.78),
    0 0 54px rgba(239, 87, 119,0.92);
  }
  to {
    text-shadow:
    0 0 6px rgba(202,228,225,0.98),
    0 0 30px rgba(202,228,225,0.42),
    0 0 12px rgba(239, 87, 119,0.58),
    0 0 22px rgba(239, 87, 119,0.84),
    0 0 38px rgba(239, 87, 119,0.88),
    0 0 60px rgba(239, 87, 119,1);
  }
}
@keyframes neon-dis {
  from {
    text-shadow:
    0 0 6px rgba(202,228,225,0.92),
    0 0 30px rgba(202,228,225,0.34),
    0 0 12px rgba(30, 39, 46,0.52),
    0 0 21px rgba(30, 39, 46,0.92),
    0 0 34px rgba(30, 39, 46,0.78),
    0 0 54px rgba(30, 39, 46,0.92);
  }
  to {
    text-shadow:
    0 0 6px rgba(202,228,225,0.98),
    0 0 30px rgba(202,228,225,0.42),
    0 0 12px rgba(30, 39, 46,0.58),
    0 0 22px rgba(30, 39, 46,0.84),
    0 0 38px rgba(30, 39, 46,0.88),
    0 0 60px rgba(30, 39, 46,1);
  }
}